@import url(https://fonts.googleapis.com/css?family=Orbitron&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  min-height: 100vh;
  display: flex;
  align-items: center;
  flex-direction: column;
  background: -webkit-gradient(linear, left top, left bottom, from(#efefef), to(#bfbfbf));
  background: -webkit-linear-gradient(#efefef, #bfbfbf);
  background: linear-gradient(#efefef, #bfbfbf)
}

.logo {
  font-family: monospace;
  font-size: 3vmax;
  letter-spacing: 0.5vw;
  -webkit-text-decoration-line: overline;
          text-decoration-line: overline;
}

.calculator {
  width: 320px;
  max-width: 100%;
  flex-shrink: 1;
  border: 2px solid #47476b;
  padding: 5px;
  background-color: #3a3a3a;
}

.author {
  text-align: center;
  margin-top: 15px;
}
.author a {
  text-decoration: none;
  color: #00264d;
  line-height: 26px;
}

#display {
  height: 3rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-end;
  font-family: Orbitron;
  background-color: #3F5666;
  padding: 10px;
  border: 1px solid #47476b;
  overflow: hidden;
}

#formula {
  color: #AAA;
}

#input {
  font-weight: 700;
  font-size: 1.6rem;
  color: #FFF;
}
#keys {
  display: grid;
  grid-template-columns: repeat(4, minmax(20px, 1fr));
  grid-template-rows: repeat(5, minmax(20px, 1fr));
  grid-gap: 15px 10px;
  justify-content: stretch;
  padding: 15px 10px 20px;
}

button {
  padding: 15px 25px;
  font-family: monospace;
  font-size: 2rem;
  text-align: center;
  cursor: pointer;
  outline: none;
  color: #f2f2f2;
  background-color: gray;
  border: none;
  border-radius: 15px;
  box-shadow: 0 9px #999;
}

/* separate class for React keyDown event   */
button:active, .buttonActive {
  box-shadow: 0 5px #666;
  -webkit-transform: translateY(4px);
          transform: translateY(4px);
}

#clear {
  grid-column: 1 / 3;
  grid-row: 1;
  background-color: #990000;
}

#zero {
  grid-column: 1 / 3;
  grid-row: 5;
}

#equals {
  grid-column: 4;
  grid-row: 4 / 6;
}

#divide {
  grid-column: 3;
  grid-row: 1;
  background-color: #666666;
}

#multiply {
  grid-column: 4;
  grid-row: 1;
  background-color: #666666;
}

#subtract {
  grid-column: 4;
  grid-row: 2;
  background-color: #666666;
}

#add {
  grid-column: 4;
  grid-row: 3;
  background-color: #666666;
}

#equals {
  grid-column: 4;
  grid-row: 4 / 6;
  background-color: #666666;
}

#one {
  grid-column: 1;
  grid-row: 4;
}

#two {
  grid-column: 2;
  grid-row: 4;
}

#three {
  grid-column: 3;
  grid-row: 4;
}

#four {
  grid-column: 1;
  grid-row: 3;
}

#five {
  grid-column: 2;
  grid-row: 3;
}

#six {
  grid-column: 3;
  grid-row: 3;
}

#seven {
  grid-column: 1;
  grid-row: 2;
}

#eight {
  grid-column: 2;
  grid-row: 2;
}

#nine {
  grid-column: 3;
  grid-row: 2;
}

